@charset "UTF-8";
/* Fonts */
/* Sizes */
@font-face {
  font-family: "PT Sans";
  font-weight: 700;
  src: url("../fonts/ptsansbold.woff2") format("woff2"), url("../fonts/ptsansbold.woff") format("woff");
}
@font-face {
  font-family: "PT Sans";
  font-weight: 400;
  src: url("../fonts/ptsans.woff2") format("woff2"), url("../fonts/ptsans.woff") format("woff");
}
html {
  height: 100%;
  box-sizing: border-box;
}

body {
  margin: 0 auto;
  min-width: 320px;
  min-height: 100vh;
  display: flex;
  font-family: "PT Sans", "Arial", sans-serif;
  flex-direction: column;
  font-size: 14px;
  line-height: 26px;
  color: #000000;
  text-transform: uppercase;
  text-align: center;
  counter-reset: section;
}

img {
  max-width: 100%;
  height: auto;
}

main {
  display: block;
  background-color: #ffffff;
}

.page-wrapper {
  margin: 0 auto;
  width: 280px;
}

.page-wrapper__index {
  margin: 0 auto;
  width: 320px;
}

