  margin-left: -51px;
}

.main-header__logo-picture {
  height: auto;
}

.main-header__top {
  justify-content: flex-end;
  align-items: center;
  height: 56px;
}

.main-header__intro {
  min-height: 390px;
  background-image: url("../img/back-mobile.jpg");
  background-color: #81b3d2;
  background-repeat: no-repeat;
  background-size: cover;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-position: bottom;
}

.main-header__title {
  width: 229px;
  height: auto;
  margin-top: -20px;
}

.main-header__intro::after {
  position: absolute;
  content: "";
  width: 100%;
  height: 58px;
  left: 0;
  bottom: 0;
  background-image: url("../img/intro-triangle-desktop.svg");
  background-repeat: no-repeat;
  background-position: center;
}

.main-header__intro-fond {
  display: none;
}

/* catalog */
.main-header__inner {
  height: 158px;
  background-position: 35% 91%;
