.main-menu {
  display: block;
}
.main-menu--opened {
  display: block;
}

.main-menu__list {
  margin: 0;
  list-style: none;
  padding: 0;
}

.main-menu__item {
  font-family: "PT Sans", "Arial", sans-serif;
  text-align: center;
  padding: 10px 0 5px 0;
  border: 0;
  width: 100%;
  display: block;
  font-size: 18px;
  line-height: 40px;
  font-weight: 700;
  color: #ffffff;
  text-transform: uppercase;
  text-decoration: none;
  background-color: #81b3d2;
  border-top: 1px solid rgba(255, 255, 255, 0.2);
  border-bottom: 1px solid rgba(0, 0, 0, 0.2);
}

.main-menu__item:hover {
  background-color: #669ec0;
}

.main-menu__item:active {
  background-color: #5496bd;
}

.main-menu__item--active:active {
  color: #766357;
}

.main-menu__link {
  text-decoration: none;
  color: #ffffff;
  line-height: 18px;
  font-size: 18px;
}

.main-menu__link:active {
  color: rgba(255, 255, 255, 0.3);
}

.main-menu--closed .main-menu__item {
  display: none;
}

.main-menu--closed {
  position: absolute;
  content: "";
  right: 0;
  width: 25px;
  height: 3px;
  cursor: pointer;
  border: none;
  z-index: 10;
}

.main-menu--opened .main-menu__button {
  background-color: #81b3d2;
}
.main-menu--opened .main-menu__button::before, .main-menu--opened .main-menu__button::after {
  content: "";
  position: absolute;
  right: 0;
  width: 25px;
  height: 3px;
  background-color: #ffffff;
}
.main-menu--opened .main-menu__button::before {
  transform: rotate(45deg);
  box-shadow: none;
}
.main-menu--opened .main-menu__button::after {
  transform: rotate(-45deg);
}
.main-menu--opened .main-menu__button:active::before, .main-menu--opened .main-menu__button:active::after {
  background-color: fade(#ffffff, 30%);
}

.main-menu__button {
  width: 30px;
  height: 30px;
  content: "";
  position: absolute;
  top: 16px;
  right: 11px;
  border: none;
  cursor: pointer;
}

.main-menu__button:hover {
  background-color: transparent;
}

.main-menu--closed .main-menu__button::before {
  content: "";
  position: absolute;
  top: 2px;
  left: 0;
  width: 23px;
  height: 3px;
  background-color: #81b3d2;
  box-shadow: 0 8px 0 0 #81b3d2, 0 16px 0 0 #81b3d2;
}

@media (min-width: 768px) {
  .main-menu__button {
    display: none;
  }

  .main-menu--opened {
    display: none;
  }

  .main-menu--closed .main-menu__button::before {
    display: none;
  }

  .main-menu {
    width: 100%;
    margin: 0;
    display: block;
    right: 0;
  }

  .main-menu--closed .main-menu__item {
    display: flex;
    justify-content: center;
  }

  .main-menu--closed {
    top: 0;
  }

  .main-menu__list {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    margin: 0;
    padding: 0;
    list-style: none;
    position: relative;
    background-color: #ffffff;
    box-shadow: none;
  }

  .main-menu__item {
    width: 16%;
    border: 0;
    border-bottom: none;
    border-top: none;
    text-align: center;
    padding: 22px 11px 23px 14px;
    box-shadow: none;
    border-radius: 0;
    background-color: transparent;
    font-size: 13px;
  }
  .main-menu__item:nth-of-type(2) {
    margin-right: 20%;
  }

  .main-menu__item:active {
    background-color: #ffffff;
  }

  .main-menu__item:hover {
    background-color: #ffffff;
  }

  .main-menu__link {
    line-height: 10px;
    font-weight: 700;
    text-decoration: none;
    color: #000000;
    background: #ffffff;
    font-size: 13px;
  }
  .main-menu__link:hover, .main-menu__link:focus {
    color: #81b3d2;
    background-color: #ffffff;
  }
  .main-menu__link:active {
    color: rgba(0, 0, 0, 0.3);
    background-color: #ffffff;
  }

  .main-menu__link:not([href]):hover,
.main-menu__link:not([href]):focus {
    color: #766357;
  }

  .main-menu__link:not([href]):active {
    color: rgba(118, 99, 87, 0.3);
  }
}
@media (min-width: 1200px) {
  .main-menu__item {
    padding: 23px 24px 21px 22px;
  }
}
